import React, {useEffect, useState, useRef} from 'react'
import SimpleImageSlider from "react-simple-image-slider";
import { 
    HeroContainer, 
    HeroBg, VideoBg, 
    HeroContent, 
    HeroH1, 
    HeroP, 
    HeroBtnWrapper, 
    ArrowForward, 
    ArrowRight,
    StyledImg,
    StyledButtonDiv,
    StyledTopButtons,
    ImgSpacer,
    StyledButtonImg,
    StyledMobileImg,
    TopSpacerBtn,
    PriceSpan,
    PriceBtnSpacer,
    LineSpacer,
    MintButton,
    SpacerTop,
    ImgWrapper

} from './HeroElements'
import {Button} from '../ButtonElement'
import Bgfull from '../../images/bgfull.png'
import ImgSample from '../../images/sample.png'
import Arrowdown from '../../images/downarrow.png';
import Arrowup from '../../images/uparrow.png';
import MobileUp from '../../images/smallup.png'
import MobileDown from '../../images/smalldown.png'
import img from '../../images/scroll/1.png'
import img2 from '../../images/scroll/2.png'
import img3 from '../../images/scroll/3.png'
import img4 from '../../images/scroll/4.png'
import img5 from '../../images/scroll/5.png'
import img6 from '../../images/scroll/6.png'
import img7 from '../../images/scroll/7.png'
import img8 from '../../images/scroll/8.png'

const HeroSection = () => {

    const images = [
         { url: img},
         { url: img2},
         { url: img3},
         { url: img4},
         { url: img6},
         { url: img7},
         { url: img8},
    ];

    const [timerDays, setTimerDays] = useState('00');
    const [timerHours, setTimerHours] = useState('00');
    const [timerMinutes, setTimerMinutes] = useState('00');
    const [timerSeconds, setTimerSeconds] = useState('00');

    let interval = useRef();

    const startTimer = () => {
        const countdownDate = new Date('September 11, 2021 20:30:00').getTime();

        interval.current = setInterval (() => {
            const now = new Date().getTime();
            const distance = countdownDate - now;

            const days = Math.floor(distance / (1000 * 60 * 60 * 24));
            const hours = Math.floor((distance % (1000 * 60 * 60 * 24) / (1000* 60 * 60)));
            const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
            const seconds = Math.floor((distance % (1000 * 60)) / 60);

            if (distance < 0) {
                clearInterval(interval.current);
            } else {
                setTimerDays(days);
                setTimerHours(hours);
                setTimerMinutes(minutes);
                setTimerSeconds(seconds);
            }

        }, 1000)
    };

    useEffect(()=> {
        startTimer();
        return () => {
            clearInterval();
        };
    });


    const [hover, setHover] = useState(false);

    const onHover = () => {
        setHover(!hover);
    }

    const [count, setCount] = useState(1);

    const decrease = () => {
        let num = count - 1;
        if(num <= 1){
            setCount(1)
        }
        else setCount(num)
    }

    const increase = () => {
        let num = count + 1;

        if(num >= 10) {
            setCount(10);
        }
        else setCount(num)
    }

    return (
        <HeroContainer>
            
            <HeroBg>
                <VideoBg src={Bgfull} alt='bg' />
            </HeroBg>
            <HeroContent>
                <ImgWrapper>
                    <SimpleImageSlider 
                            width={241}
                            height={242}
                            images={images}
                            navStyle = {5}
                            navSize = {50}
                            showNavs = {false}
                            navMargin = {-15}
                            showBullets = {true}
                            
                    />
                </ImgWrapper>
                {/* <StyledImg src={ImgSample} alt='sample'/> */}
                <ImgSpacer/>
                {/* <HeroH1>Lorem ipsum</HeroH1>
                <HeroP>
                loremipsum loremipsum loremipsum loremipsum loremipsum loremipsum loremipsum loremipsumloremipsum loremipsum loremipsum loremipsum loremipsum 
                loremipsum loremipsum loremipsum loremipsum loremipsum loremipsum loremipsum loremipsumloremipsum loremipsum loremipsum loremipsum loremipsum ,
                </HeroP>
                <HeroBtnWrapper>
                    <Button to="signup" onMouseEnter={onHover} onMouseLeave={onHover} primary='true' dark='true'>
                        Get started {hover ? <ArrowForward /> : <ArrowRight/>}
                    </Button>
                </HeroBtnWrapper> */}
                <StyledButtonDiv>
                    <StyledTopButtons>
                        <HeroBtnWrapper onClick = {()=>increase()}>
                            <StyledButtonImg src={Arrowup} alt='up'/>
                            <StyledMobileImg src={MobileUp} alt='mobileup'/>
                        </HeroBtnWrapper>
                        <TopSpacerBtn/>
                        <HeroBtnWrapper onClick={()=>decrease()}>
                            <StyledButtonImg src={Arrowdown} alt='down'/>
                            <StyledMobileImg src={MobileDown} alt='mobiledown'/>
                        </HeroBtnWrapper>
                        <PriceBtnSpacer/>
                        <PriceSpan>{count}</PriceSpan>
                    </StyledTopButtons>
                    <LineSpacer/>
                    <MintButton disabled={true}>{timerDays}Days {timerHours}Hours {timerMinutes}Minutes</MintButton>
                </StyledButtonDiv>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection
