import styled from 'styled-components'
import {MdKeyboardArrowRight, MdArrowForward} from 'react-icons/md'

export const HeroContainer = styled.div`
background: #0c0c0c;
display: flex;
justify-content: center;
align-items: center;
padding: 0 120px;
width: 100%;
height: 1200px;
background-color: black;
position: relative;
z-index: 1;

:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 100%),
    linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%);
    z-index: 2; 
}
`
export const SpacerTop = styled.div`
height: 80px;
`

export const HeroBg = styled.div`
position: absolute;
/* top: 0;
right: 0;
bottom: 0;
left: 0; */
width: 100%;
max-width: 1440px;
display: flex;
justify-content: center;
align-items: center;
height: 100%;
background-size: auto;
overflow: hidden;
`

export const VideoBg = styled.img `
width: 100%;
height: 100%;

@media screen and (max-width: 1050px) {
-o-object-fit: cover;
object-fit: cover; 

}

@media screen and (max-width: 480px) {
-o-object-fit: cover;
object-fit: cover; 

}
/* -o-object-fit: cover;
object-fit: cover;  */
`

export const HeroContent = styled.div`
z-index: 3;
max-width: 967px;
max-height: 345px;
position: absolute;
display: flex;
flex-direction: row;
align-items: center;

padding: 52px;
background-color: black;

background: linear-gradient(115.5deg, #000000 27.97%, rgba(0, 0, 0, 0.45) 85.17%);

@media screen and (max-width: 1050px) {
    max-width: 327px;
    flex-direction: column;
    padding: 28px;
    max-height: 506px;
}

`

export const HeroH1 = styled.h1`
color: #fff;
font-size: 48px;
text-align: center;

@media screen and (max-width: 768px) {
    font-size: 40px;
}

@media screen and (max-width: 480px) {
    font-size: 32px;
}
`

export const HeroP = styled.p`
margin-top: 24px;
color: #fff;
font-size: 24px;
text-align: center;
max-width: 600px;

@media screen and (max-width: 768px) {
    font-size: 24px;
}

@media screen and (max-width: 480px) {
    font-size: 18px;
}
`

export const HeroBtnWrapper = styled.button `
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
    height: 84px;
    width: 84px;
@media screen and (max-width: 1050px) {
width: 48px;
height: 48px;

}
`

export const ArrowForward = styled(MdArrowForward)`
margin-right: 8px;
font-size: 20px;
`
export const ArrowRight = styled(MdKeyboardArrowRight)`
margin-right: 8px;
font-size: 20px;
`

export const StyledImg = styled.img`
width: 242px;
height: 241px;

@media screen and (max-width: 1050px) {
width: 271px;
height: 269px;
}
`

export const ImgWrapper = styled.div `

max-width: 242px;
max-height: 241px;

@media screen and (max-width: 1050px) {
width: 271px;
height: 269px;
}
`

export const ImgSpacer = styled.div`
width: 47px;

@media screen and (max-width: 1050px) {
    height: 24px;
}

`

export const StyledButtonDiv = styled.div `
height: 241px;
width: 573px;

position: relative;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
@media screen and (max-width: 1050px) {
    width: 271px;
    height: 157px;
}

`

export const StyledTopButtons = styled.div`
width: 573px;
height: 100px;
/* border: 1px solid green; */
/* align-items: center;
justify-content: center; */
display: flex;
flex-direction: row;
@media screen and (max-width: 1050px) {
width: 271px;
height: 48px;
}

`
export const StyledButtonImg = styled.img`
width: 84px;
height: 84px;

-webkit-user-select: none;
-khtml-user-select: none;
-moz-user-select: none;
-o-user-select: none;
-ms-user-select: none;
user-select: none;

-webkit-user-drag: none;
-khtml-user-drag: none;
-moz-user-drag: none;
-o-user-drag: none;
-ms-user-drag: none;

&:hover{
    transform: scale(1.02);
    transition: all 0.2s ease-in-out;
}
@media screen and (max-width: 1050px) {
display: none;
}
`

export const StyledMobileImg = styled.img `
display: none;
@media screen and (max-width: 1050px) {
display: block;
width: 48px;
height: 48px;
-webkit-user-select: none;
-khtml-user-select: none;
-moz-user-select: none;
-o-user-select: none;
-ms-user-select: none;
user-select: none;

-webkit-user-drag: none;
-khtml-user-drag: none;
-moz-user-drag: none;
-o-user-drag: none;
-ms-user-drag: none;

&:hover{
    transform: scale(1.02);
    transition: all 0.3s ease-in-out;
}
}
`

export const TopSpacerBtn = styled.div`

width: 16px;

@media screen and (max-width: 1050px) {
width: 8px;
}
`

export const PriceSpan = styled.span `
height: 84px;
width: 357px;
margin: 0rem;
padding: 0rem;
font-family: Heebo;
font-style: normal;
font-weight: normal;
font-size: 36px;
line-height: 53px;
/* identical to box height */
justify-content: center;
display: flex;
align-items: center;
text-align: center;
letter-spacing: 0.04em;
border: 1px solid #505050;

/* Plain White */

color: #FFFFFF;


@media screen and (max-width: 1050px) {
    width: 159px;
    height: 48px;
    font-family: Heebo;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 29px;
    align-items: center;
    text-align: center;
    letter-spacing: 0.04em;
}

`

export const PriceBtnSpacer = styled.div`
width: 32px;

@media screen and (max-width: 1050px) {
width: 8px;

}

`

export const LineSpacer = styled.div`
width: 573px;
height: 0px;
margin-top: 28px;
margin-bottom: 28px;
border: 1px solid #505050;


@media screen and (max-width: 1050px) {
width: 271px;
height: 0px;
margin-top: 24px;
margin-bottom: 24px;
}
`

export const MintButton = styled.button`
width: 573px;
height: 101px;
background: #DFA92C;
font-family: Heebo;
font-style: normal;
font-weight: 800;
font-size: 28px;
line-height: 41px;
text-align: center;
letter-spacing: 0.04em;
text-transform: uppercase;
color: #FFFFFF;
cursor: pointer;

&:hover{
    filter: brightness(75%);
    transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 1050px) {
width: 272px;
height: 61px;
font-size: 15px; //change
}
`