import styled from 'styled-components'
import { Link as LinkR } from 'react-router-dom'
import { Link as LinkS } from 'react-scroll';



export const Nav = styled.nav`
background: ${({scrollNav}) => (scrollNav ? '#000' : 'transparent')};
height: 80px;
margin-top: -80px;

display: flex;
justify-content: center;
align-items: center;
font-size: 1rem;
position: sticky;
top: 0;
z-index: 10;

@media screen and (max-width: 960px){
    transition: 0.8s all ease;
}
`

export const NavbarContainer = styled.div`
display: flex;
justify-content: space-between;
height: 80px;
z-index: 1;
width: 100%;
padding: 16px 12px;
max-width: 1440px;
`

export const NavLogo = styled(LinkR)`
color: #fff;
/* justify-self: flex-start; */
justify-content: space-between;
cursor: pointer;
font-size: 1.5rem;
display: flex;
align-items: center;
font-weight: bold;
text-decoration: none;

&:hover {
    transition: 0.3s ease-in-out;
    color: #DFA92C;
}

`

export const MobileIcon = styled.div `
display: none;

@media screen and (max-width: 810px){
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff;
}
`

export const NavMenu = styled.div `
display: flex;
align-items: center;
list-style: none;

@media screen and (max-width: 810px) {
    display: none;
}
`

export const NavItem = styled.li `
height: 60px;
`
export const NavLinks = styled(LinkS)`
color: #fff;
display: flex;
align-items: center; 
text-decoration: line-through; // change to none
padding: 0 1rem;
height: 100%;
cursor: pointer;
letter-spacing: 0.08em;
text-transform: uppercase;
font-family: Heebo;
font-style: normal;

&:hover {
    transition: 0.3s ease-in-out;
    color: #DFA92C;
}
`

export const NavBtn = styled.button`
display: flex;
white-space: nowrap;
align-items: center;
background: transparent;
box-shadow: 0px 0px 0px transparent;
border: 0px solid transparent;
text-shadow: 0px 0px 0px transparent;

font-family: Heebo;
font-style: normal;
font-weight: 500;
font-size: 16px;
letter-spacing: 0.08em;
text-decoration-line: line-through; //change to underline;
text-transform: uppercase;

color: #DFA92C;
cursor: pointer;

:hover{
    transition: 0.3s ease-in-out;
    color: #fff;
}
@media screen and (max-width: 810px) {
    display: none;
}
`
export const NavBtnLink = styled(LinkR)`
border-radius: 50px;
background: #01bf71;
white-space: nowrap;
padding: 10px 22px;
color: #010606;
font-size: 16px;
outline: none;
cursor: pointer;
transition: all 0.2s ease-in-out;


&:hover {
    transition: all 0.2s ease-in-out;
    background: #fff;
    color: #010606;
}
`

export const StyledImg = styled.img `
height: 40px;
width: 40px;
`

export const Spacer = styled.div`
width: 20px;
`