import React from 'react'
import {FaFacebook, FaInstagram, FaTelegram, FaTwitter} from 'react-icons/fa'
import {animateScroll as scroll} from 'react-scroll'
import {
    FooterContainer,
    FooterWrap,
    FooterLinksContainer,
    FooterLinksWrapper,
    FooterLinkItems,
    FooterLinkTitle,
    FooterLink,
    SocialMedia,
    SocialMediaWrap,
    SocialLogo,
    WebsiteRights,
    SocialIcons,
    SocialIconLink
}
from './FooterElements'
const Footer = () => {

    const toggleHome = () => {
        scroll.scrollToTop();
    };

    return (
        <FooterContainer>
            <FooterWrap>
                <SocialMedia>
                    <SocialMediaWrap>
                        {/* <SocialLogo to='/' onClick={toggleHome}>
                            dolla
                        </SocialLogo> */}
                        <WebsiteRights>© NFT ART DAWGZ COLLECTION</WebsiteRights>
                        <SocialIcons>
                        <SocialIconLink href="//twitter.com/DawgzCollection" target="//twitter.com/DawgzCollection" aria-label="Twitter">
                            Twitter
                        </SocialIconLink>
                        {/* <SocialIconLink href="/" target="_blank" aria-label="Instagram">
                            Telegram
                        </SocialIconLink> */}
                        <SocialIconLink href="//discord.gg/jTPkdDZMBK" target="discord.gg/jTPkdDZMBK" aria-label="Discord">
                            Discord
                        </SocialIconLink>
                        <SocialIconLink href="/" target="_blank" aria-label="OpenSea">
                            OpenSea
                        </SocialIconLink>
                        </SocialIcons>
                    </SocialMediaWrap>
                </SocialMedia>
            </FooterWrap>
        </FooterContainer>
    )
}

export default Footer
