import React from 'react'
import {SidebarContainer, Icon, CloseIcon, SidebarWrapper, SidebarMenu, SidebarLink, SideBtnWrap, SidebarRoute} from './SidebarElements'

const Sidebar = ({ isOpen, toggle }) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon/>
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLink to="about" onClick={toggle}>Home</SidebarLink>
                    <SidebarLink to="discover" onClick={toggle}>About</SidebarLink>
                    <SidebarLink to="services" onClick={toggle}>Rare Card</SidebarLink>
                    <SidebarLink to="signup" onClick={toggle}>Roadmap</SidebarLink>
                    <SidebarLink to="signup" onClick={toggle}>Our Team</SidebarLink>
                </SidebarMenu>
                <SideBtnWrap>
                    Connect Wallet
                </SideBtnWrap>
            </SidebarWrapper>
        </SidebarContainer>
    )
}

export default Sidebar
